* {
  box-sizing: border-box;
}

.App {
  padding: 20px;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

.shadow {
  border-radius: 4%;

  transition: box-shadow 0.2s ease;
}

.shadow.selected {
  box-shadow: 0px 0px 5px 2px rgb(64, 0, 255);
}

.shadow.in-enemy:not(.selected) {
  box-shadow: 0px 0px 5px 2px #9f0e03;
}

.shadow.outside > .card {
  opacity: 0.1;
}

.shadow.in-enemy > .card {
  opacity: 0.6;
}
